import { Table } from 'antd';
import React from 'react';
import styled from 'styled-components';

const CitiesListContainer = styled.div`
  margin: 20px 40px;
`;

const columns = [
  {
    title: 'Nombre',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Telefono',
    dataIndex: 'phone',
    key: 'phone',
  },
  {
    title: 'Identificación',
    dataIndex: 'numberId',
    key: 'numberId',
  },
  {
    title: 'Activo',
    dataIndex: 'isActive',
    key: 'isActive',
    render: (isActive) => {
      return isActive ? 'Activo' : 'Inactivo';
    },
  },
];

const CitiesList = ({ drivers }) => {
  return (
    <CitiesListContainer>
      <Table dataSource={drivers} columns={columns} />
    </CitiesListContainer>
  );
};

export default CitiesList;
